.slider-label {
  position: absolute;
  top: -40px;
  text-align: center;
  background-color: #fff;
  color: #323c47;
  border: 1px solid #cedee8;
  border-radius: 4px;
  padding: 7px;
  opacity: 0;
  box-sizing: border-box;
  font-size: 10px;
  z-index: 999;
  width: 50px;
  left: -21.5px;
  visibility: hidden;
}
.slider-label:before {
  position: absolute;
  display: block;
  content: '';
  $start-direction: 0;
  bottom: -7px;
  left: 18px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 7px 7px 0 7px;
  border-color: #cedee8 transparent transparent transparent;
}
.slider-label:after {
  position: absolute;
  display: block;
  content: '';
  $start-direction: 0;
  bottom: -6px;
  left: 18px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 7px 7px 0 7px;
  border-color: #fff transparent transparent transparent;
}
.slider-fill {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #cedee8;
  height: 100%;
}



